export class NodeDetailComponent {
    constructor(node_, actionbar_, activatedRoute_, notifications_) {
        this.node_ = node_;
        this.actionbar_ = actionbar_;
        this.activatedRoute_ = activatedRoute_;
        this.notifications_ = notifications_;
        this.endpoint_ = EndpointManager.resource(Resource.node);
        this.isInitialized = false;
    }
    ngOnInit() {
        const resourceName = this.activatedRoute_.snapshot.params.resourceName;
        this.podListEndpoint = this.endpoint_.child(resourceName, Resource.pod);
        this.eventListEndpoint = this.endpoint_.child(resourceName, Resource.event);
        this.nodeSubscription_ = this.node_
            .get(this.endpoint_.detail(), resourceName)
            .subscribe((d) => {
            this.node = d;
            this.notifications_.pushErrors(d.errors);
            this.actionbar_.onInit.emit(new ResourceMeta('Node', d.objectMeta, d.typeMeta));
            this.isInitialized = true;
        });
    }
    ngOnDestroy() {
        this.nodeSubscription_.unsubscribe();
        this.actionbar_.onDetailsLeave.emit();
    }
    getAddresses() {
        return this.node.addresses.map((address) => `${address.type}: ${address.address}`);
    }
    getTaints() {
        return this.node.taints.map((taint) => {
            return taint.value
                ? `${taint.key}=${taint.value}:${taint.effect}`
                : `${taint.key}=${taint.effect}`;
        });
    }
}
